import { Component, OnInit } from "@angular/core";
import { Router, ActivatedRoute, ParamMap } from "@angular/router";

import { AuthService } from "../../shared/user/auth.service";
@Component({
  selector: "app-reset-form",
  templateUrl: "./reset-form.component.html",
  styleUrls: ["./reset-form.component.scss"]
})
export class ResetFormComponent implements OnInit {
  loading = false;
  error = "";
  resetFormToggle = false;
  constructor(private router: Router, private authService: AuthService) {}

  ngOnInit() {}

  sendEmailForPasswordReset(e) {
    const email = e.target.elements[0].value;

    this.loading = true;
    this.authService.resetPasswordlinkCreate(email).subscribe(
      result => {
        if (result === true) {
          this.loading = false;
          this.resetFormToggle = true;
        }
      },
      error => {
        this.error = "We have no record of this email";
        this.loading = false;
      }
    );
  }
}



